// flex
.flex-container{
  display: flex;
  flex-wrap: wrap;
}
.space-between{
  justify-content: space-between;
}
.center{
  justify-content: center;
}
.align-center{
  align-items: center;
}
.column{
  flex-direction: column;
}

// float
.float-right{
  float: right;
}
.float-left{
  float: left;
}

// display
.inline-block{
  display: inline-block;
}